import React, {Component} from "react";
import {Feature} from "eshop/modules/configurator/components/lp/offers/newOfferBoxComponents/Feature";

export class ClassificationAttributesBox extends Component {

    render() {
        return (
            <div className="u-padding-l-xl u-padding-top-l-xl">
                <div className="js-layout-fixer-group-classification-attribute">
                    <div className="js-height-sensitive-element">
                        {
                            this.props.featuresGroup.map((feature,index,array) =>
                                <Feature
                                    key={feature.code + this.props.id}
                                    removeClassFromLast={true}
                                    feature={feature}
                                    className="u-padding-l"
                                    length={array.length}
                                    index={index}
                                    processType={this.props.processType}
                                    convergence={this.props.convergence} />
                            )
                        }
                    </div>
                </div>
            </div>
        )
    }
}